	module.exports = {"data":{"markdownRemark":{"html":"<h2>Np Completeness</h2>\n<p>NP-Complete is a property of certain types of problems. If a problem is NP-Complete, it means that there is no efficient (polynomial) algorithm to find a solution to it quickly. However, if a solution is given to us, we can quickly (in polynomial time) verify that it is correct.</p>\n<p>To be more concrete, let us examine a known NP-Complete problem - the subset sum problem. Say we have a set of integers {−7, −3, −2, 5, 8}. We want to find a subset of these integers whose elements sum up to 0. How can we do that?</p>\n<p>One way would be to simply enumerate all possible subsets and check if their elements sum up to 0. This would be exponentially complex.</p>\n<p>And indeed, there is no better algorithm known, which improves on the exponential time bound. That is why it is classifies as an NP-Complete problem.</p>\n<p>There are many such known problems apart from the subset sum problem which are known to be NP-Complete. If an efficient algorithm for one is found, it implies that we can design an efficient algorithm for all problems which are NP-Complete.</p>\n<p>If you have a problem that can be proved to be NP-complete, you should stop trying to find more efficient algorithms for it and instead use heuristics to solve the problem for most test cases, or solve an approximate version of the problem. Or maybe examine the problem you’re solving to see if it can’t be simplified to something that isn’t NP-Complete.</p>\n<h4>More Information:</h4>\n<p><a href=\"https://www.ics.uci.edu/~eppstein/161/960312.html\">https://www.ics.uci.edu/~eppstein/161/960312.html</a>\n<a href=\"https://stackoverflow.com/questions/210829/what-is-an-np-complete-in-computer-science\">https://stackoverflow.com/questions/210829/what-is-an-np-complete-in-computer-science</a></p>","fields":{"slug":"/computer-science/np-completeness/"},"frontmatter":{"title":"Np Completeness"}}},"pathContext":{"slug":"/computer-science/np-completeness/"}}

